	module.exports = {"data":{"markdownRemark":{"html":"<h2>Where to Get Fonts</h2>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<p>Online font services like Google Fonts provide an easy way to use different fonts on your site without the need to worry about whether or not the person viewing your site will have the font available on their system. To use Google Fonts in your site, visit the <a href=\"https://fonts.google.com/\">Google Fonts</a> archive. You can browse the site to find the font that works best for your project. Once you’ve chosen, click the plus sign next to the font. A bar will appear at the bottom of the screen. Click on it. You will then be given several lines of code. Copy and paste the line of HTML into the head of your HTML file above the existing <link> element. Then take the CSS and use it where necessary in your stylesheet. </p>\n<p>Alternatively, you can either download TrueType or OpenType fonts from DaFont or 1001 Free Fonts and link them to your stylesheets\nYou’re done! You successfully have new fonts for your site.</p>\n<h4>Web Font Generators</h4>\n<p>Sometimes you will come accross the need to generate a webfont when you are asked to use a specific font which doesn’t have a web font. For such a problem there are generators out there that can solve this problem. Some are listed below.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Learn/CSS/Styling_text/Web_fonts\">Web fonts overview from Mozilla Developer</a></p>\n<h5>Additional Resources:</h5>\n<p>..* <a href=\"http://fonts.google.com\" target=\"_blank\">Google Fonts</a>\n..* <a href=\"http://www.fontspace.com\" target=\"_blank\">FontSpace</a>\n..* <a href=\"http://fontsquirrel.com\" target=\"_blank\">Font Squirrel</a>\n..* <a href=\"http://www.dafont.com\" target=\"_blank\">DaFont</a>\n..* <a href=\"http://www.1001freefonts.com\" target=\"_blank\">1001 Free Fonts</a>\n..* <a href=\"https://www.myfonts.com/WhatTheFont/\">What The Font</a></p>\n<h5>Web Font Generators</h5>\n<p>..* <a href=\"https://www.fontsquirrel.com/tools/webfont-generator\" target=\"_blank\">Font Squirrel Font Generator</a>\n..* <a href=\"https://transfonter.org/\" target=\"_blank\">Transfonter</a>\n..* <a href=\"https://www.web-font-generator.com/\" target=\"_blank\">Web Font Generator</a></p>","fields":{"slug":"/typography/where-to-get-fonts/"},"frontmatter":{"title":"Where to Get Fonts"}}},"pathContext":{"slug":"/typography/where-to-get-fonts/"}}

